import React, { Component } from "react";

export default class Square extends Component {
  constructor(props) {
    super(props);
    this.highlighted = "highlighted square";
  }

  getSquareColor() {
    this.props.color === "w"
      ? (this.sqrColor = "light square")
      : (this.sqrColor = "dark square");
    if (this.props.isHighlighted === true) this.sqrColor = "highlighted square";
  }

  render() {
    return (
      <div
        id={this.props.notation}
        onClick={(this.isHighlighted = true)}
        className={!this.isHighlighted ? this.sqrColor : this.highlighted}
        style={{
          left: this.props.x,
          top: this.props.y,
          width: this.props.size,
          height: this.props.size
        }}
      ></div>
    );
  }
}

