import { King, Queen, Rook, Bishop, Knight, Pawn } from "./Components/Piece";
import Square from "./Components/Square";

/* A file containing useful generic functions */
export class Chess {
  constructor(fen) {
    this.fen = fen;
    this.board = [64];
    this.fileToNum = ["a", "b", "c", "d", "e", "f", "g", "h"];
    this.squares = [];
    this.pieces = [];
    this.currentlySelected = "";
  }

  move(piece, fromSqr, toSqr) {
    this.board[toSqr] = {
      piece: piece,
      from: fromSqr,
      to: toSqr
    };

    this.board[fromSqr] = {
      piece: null,
      from: null,
      to: null
    };
  }

  parseFen() {
    var rows = this.fen.split("/");
    var end = rows[7];
    //end = end.substr(0, end.indexOf(" "));
    end = end.split(" ");
    rows[7] = end[0];
    this.turn = end[1];

    this.parseCastling(end[2]);

    this.enEnpassant = end[3];

    console.log(rows);

    var startFile = 56;
    for (var i = 0; i < 8; i++) {
      var row = this.parseRow(rows[i]);
      this.rowToBoard(row, startFile);
      startFile -= 8;
    }
    console.log(this.board);
  }

  parseRow(row) {
    var newRowStr = "";
    var i = 0;

    while (i < row.length) {
      if (this.is_numeric(row[i])) {
        var num = row[i];
        newRowStr += "0".repeat(num);
      } else if (/[KkQqNnBbRrPp]/.test(row[i])) {
        newRowStr += row[i];
      }
      i++;
    }
    console.log(newRowStr);
    return newRowStr;
  }

  rowToBoard(row, startFile) {
    for (var i = 0; i < 8; i++) {
      if (row[i] === "0") {
        this.board[startFile + i] = null;
      } else {
        this.board[startFile + i] = row[i];
      }
    }
  }

  parseCastling(str) {
    this.wKcastling = str.includes("K");
    this.bKCastling = str.includes("k");
    this.wQcastling = str.includes("Q");
    this.bQcastling = str.includes("q");
  }

  is_numeric(str) {
    return /^\d+$/.test(str);
  }

  notationToIndex(notation) {
    return this.fileToNum.indexOf(notation[0]) * 7 + notation[1];
  }

  indexToNotation(index) {
    var file = this.fileToNum[Math.floor(index / 8)];
    var rank = (index % 8) + 1;
    return file + rank.toString();
  }

  // to get x, y of element
  cumulativeOffset = function (element) {
    var top = 0,
      left = 0;
    do {
      top += element.offsetTop || 0;
      left += element.offsetLeft || 0;
      element = element.offsetParent;
    } while (element);

    return {
      top: top,
      left: left
    };
  };

  getPieceComponent(piece, x, y) {
    var color;
    /[A-Z]/.test(piece) ? (color = "w") : (color = "b");
    if (piece != null) piece = piece.toUpperCase();
    else return null;

    switch (piece) {
      case "K":
        return (
          <King
            size={"64px"}
            x={x + "px"}
            y={y + "px"}
            color={color}
            chess={this}
          />
        );
      case "Q":
        return (
          <Queen
            size={"64px"}
            x={x + "px"}
            y={y + "px"}
            color={color}
            chess={this}
          />
        );
      case "R":
        return (
          <Rook
            size={"64px"}
            x={x + "px"}
            y={y + "px"}
            color={color}
            chess={this}
          />
        );
      case "B":
        return (
          <Bishop
            size={"64px"}
            x={x + "px"}
            y={y + "px"}
            color={color}
            chess={this}
          />
        );
      case "N":
        return (
          <Knight
            size={"64px"}
            x={x + "px"}
            y={y + "px"}
            color={color}
            chess={this}
          />
        );
      case "P":
        return (
          <Pawn
            size={"64px"}
            x={x + "px"}
            y={y + "px"}
            color={color}
            chess={this}
          />
        );
      default:
        throw new Error("Error: Unknown piece found.");
    }
  }

  generateBoard() {
    this.parseFen();
    var color = "b";

    var i = 0;
    for (var j = 63; j >= 0; j--) {
      var square = this.indexToNotation(i);

      if (i % 8 === 0) {
        color === "w" ? (color = "w") : (color = "b");
      } else {
        color === "w" ? (color = "b") : (color = "w");
      }

      const x = (i % 8) * 64;
      const y = Math.floor(i / 8) * 64;

      var piece = this.getPieceComponent(this.board[j], x, y);

      //var isHighlighted = false;

      this.squares.push(
        <Square
          id={square}
          color={color}
          size={"64px"}
          x={x + "px"}
          y={y + "px"}
          isHighlighted={false}
        />
      );
      this.pieces.push(piece);
      i++;
    }
  }
}
