import React, { Component } from "react";
import "./chessboard.css";
import wPawn from "../Images/Pieces/White/wPawn.png";
import bPawn from "../Images/Pieces/Black/bPawn.png";
import wKing from "../Images/Pieces/White/wKing.png";
import bKing from "../Images/Pieces/Black/bKing.png";
import wQueen from "../Images/Pieces/White/wQueen.png";
import bQueen from "../Images/Pieces/Black/bQueen.png";
import wKnight from "../Images/Pieces/White/wKnight.png";
import bKnight from "../Images/Pieces/Black/bKnight.png";
import wBishop from "../Images/Pieces/White/wBishop.png";
import bBishop from "../Images/Pieces/Black/bBishop.png";
import wRook from "../Images/Pieces/White/wRook.png";
import bRook from "../Images/Pieces/Black/bRook.png";

export default class Piece extends Component {
  constructor(props) {
    super(props);
    this.color = props.color;
    this.size = props.size;
    this.x = props.x;
    this.y = props.y;
    this.index = this.y * 8 + this.x;
    this.selectPiece = this.selectPiece.bind(this);
    this.chess = this.props.chess;
  }

  getImgSrc() {
    var folder = this.color === "w" ? "White/" : "Black/";
    return (
      "../Images/Pieces/" + folder + this.color + this.constructor.name + ".png"
    );
  }

  selectPiece() {
    //this.chess.squares[5].setHighlight(true);
    //console.log(this.chess.squares[5]);
    //document.body.squares.style = "yellow";
  }
}

export class King extends Piece {
  constructor(props) {
    super(props);
    this.isSliding = false;
    this.moveShifts = [8, -8, 1, -1];
    this.selectPiece = this.selectPiece.bind(this);
    this.chess = this.props.chess;
  }

  render() {
    return (
      <img
        className="piece unselectable deselector"
        src={this.color === "w" ? wKing : bKing}
        alt=""
        style={{
          left: this.x,
          top: this.y,
          width: this.size,
          height: this.size
        }}
        onClick={this.selectPiece}
      />
    );
  }
}

