export class Pawn extends Piece {
  constructor(props) {
    super(props);
    this.isSliding = false;
    this.getMoveShifts();
  }

  getMoveShifts() {
    return this.color === "w" ? [8, 16] : [-8, -16];
  }

  render() {
    return (
      <img
        className="piece unselectable deselector"
        src={this.color === "w" ? wPawn : bPawn}
        alt=""
        style={{
          left: this.x,
          top: this.y,
          width: this.size,
          height: this.size
        }}
      />
    );
  }
}
