import React, { Component } from "react";
import ReactDOM from "react-dom";
import "./chessboard.css";
import { King, Queen, Rook, Bishop, Knight, Pawn } from "./Piece";
import Square from "./Square";

export default class Chessboard extends Component {
  constructor(props) {
    super(props);
    this.chess = this.props.chess;
    this.state = { fen: this.props.fen };
    this.chess.generateBoard();
    this.sqrSize = this.props.sqrSize;
  }

  findSquareCoordinates(notation) {
    var x = this.chess.fileToNum[notation[0]];
    return [x * 64, notation[1] * 64];
  }

  render() {
    return (
      <div>
        {this.chess.squares}
        {this.chess.pieces}
      </div>
    );
  }
}
